import React, { Component } from "react";
import "../App.css";

let num = 3;
export default function Header() {
  return (
    <header className="w-[100%] h-[70px] fixed z-[9] top-0 left-0 bg-transparent header">
      <Nav />
      <NavMobile />
    </header>
  );
}

function Nav() {
  return (
    <nav className="w-full h-full flex justify-between items-center px-[6%]">
      <MenuNav />
      <LogoNav />
    </nav>
  );
}

class MenuNav extends Component {
  constructor() {
    super();
    this.MenuText = [
      {
        txt1: "Home",
        txt2: "About",
        txt3: "Menu",
        txt4: "Blogs",
        txt5: "Contact",
        l1: "Home.php",
        l2: "About.php",
        l3: "Menu.php",
        l4: "Blogs.php",
        l5: "Contact.php",
      },
    ];
  }

  Mobile = () => {
    document.getElementById("NavMobile").style.display = "flex";
    setTimeout(() => {
      document.getElementById("nav").classList.remove("w-[0]");
      document.getElementById("nav").classList.add("w-[50vw]");
    }, 1200);
  };

  componentDidMount() {
    window.addEventListener("scroll", () => {
      if (
        window.scrollY > document.getElementsByClassName("header")[0].offsetTop
      ) {
        document
          .getElementsByClassName("header")[0]
          .classList.remove("bg-transparent");
        document
          .getElementsByClassName("header")[0]
          .classList.add("bg-[#222b45]", "shadow-sm", "shadow-slate-300");
      } else {
        document
          .getElementsByClassName("header")[0]
          .classList.remove("bg-[#222b45]", "shadow-sm", "shadow-slate-300");
        document
          .getElementsByClassName("header")[0]
          .classList.add("bg-transparent");
      }
    });
  }
  render() {
    return (
      <>
        {this.MenuText.map((val, index) => {
          return (
            <ul className="hidden xl:flex w-1/2 h-full justify-center items-center">
              <li key={index} className="grow flex items-center justify-center">
                <a
                  href={val.l1}
                  className="w-fit h-fit no-underline text-white hover:text-green-400 ease-in-out duration-500"
                >
                  {val.txt1}
                </a>
              </li>
              <li key={index} className="grow flex items-center justify-center">
                <a
                  href={val.l2}
                  className="w-fit h-fit no-underline text-white hover:text-green-400 ease-in-out duration-500"
                >
                  {val.txt2}
                </a>
              </li>
              <li key={index} className="grow flex items-center justify-center">
                <a
                  href={val.l3}
                  className="w-fit h-fit no-underline text-white hover:text-green-400 ease-in-out duration-500"
                >
                  {val.txt3}
                </a>
              </li>
              <li key={index} className="grow flex items-center justify-center">
                <a
                  href={val.l4}
                  className="w-fit h-fit no-underline text-white hover:text-green-400 ease-in-out duration-500"
                >
                  {val.txt4}
                </a>
              </li>
              <li key={index} className="grow flex items-center justify-center">
                <a
                  href={val.l5}
                  className="w-fit h-fit no-underline text-white hover:text-green-400 ease-in-out duration-500"
                >
                  {val.txt5}
                </a>
              </li>
            </ul>
          );
        })}
        <div className="flex xl:hidden items-center justify-center w-1/6">
          <span
            id="mobile"
            onClick={this.Mobile}
            className="text-white hover:text-green-400 ease-in-out duration-500 text-[1.3rem] rotate-90 line cursor-pointer"
          >
            |||
          </span>
        </div>
      </>
    );
  }
}

function LogoNav() {
  return (
    <figure className="h-full flex items-center justify-end xl:justify-center w-1/2 xl:w-1/6">
      <figcaption className="text-white text-[1.5rem]">
        <span className="text-green-400">S</span>aman-
        <span className="text-green-400">T</span>ofighian
      </figcaption>
    </figure>
  );
}

function NavMobile() {
  const Close = () => {
    document.getElementById("nav").classList.remove("w-[50vw]");
    document.getElementById("nav").classList.add("w-[0]");
    setTimeout(() => {
      document.getElementById("NavMobile").style.display = "none";
    }, 1200);
  };

  return (
    <section
      className="w-[100%] h-[100vh] bg-[#00000092] fixed top-0 left-0 z-[99]"
      id="NavMobile"
    >
      <nav className="bg-[#fff] h-[100dvh] w-[0]" id="nav">
        <div className="w-[100%] flex justify-end">
          <span
            onClick={Close}
            className="text-red-600 cursor-pointer text-[2rem] py-3 px-5"
          >
            X
          </span>
        </div>
        <ul className="w-[100%] mt-7 h-full flex flex-wrap">
          <li className="w-full flex justify-center grow">
            <a
              href="#"
              className="w-fit h-fit text-black hover:text-green-400 ease-in-out duration-500"
            >
              Home
            </a>
          </li>
          <li className="w-full flex justify-center grow">
            <a
              href="#"
              className="w-fit h-fit text-black hover:text-green-400 ease-in-out duration-500"
            >
              About
            </a>
          </li>
          <li className="w-full flex justify-center grow">
            <a
              href="#"
              className="w-fit h-fit text-black hover:text-green-400 ease-in-out duration-500"
            >
              Menu
            </a>
          </li>
          <li className="w-full flex justify-center grow">
            <a
              href="#"
              className="w-fit h-fit text-black hover:text-green-400 ease-in-out duration-500"
            >
              Blogs
            </a>
          </li>
          <li className="w-full flex justify-center grow">
            <a
              href="#"
              className="w-fit h-fit text-black hover:text-green-400 ease-in-out duration-500"
            >
              Contacts
            </a>
          </li>
        </ul>
      </nav>
    </section>
  );
}
