import React, { Component } from "react";

export default function Footer() {
  return (
    <footer className="w-[100%] bg-[#222B45] border-t-2 border-[#aeb0b449]">
      <div className="row w-[100%] px-[6%] py-9">
        <Text />
      </div>
    </footer>
  );
}

class Text extends Component {
  constructor() {
    super();
    this.state = {
      Year: new Date(),
    };
    this.setState({
      Year: new Date(),
    });
  }
  render() {
    return (
      <div className="w-full text-center py-3">
        <h2 className="text-white leading-[35px]">
          Copyright {this.state.Year.getFullYear()}, Developed By{" "}
          <strong className="text-green-400">
            <em>Saman Tofighian</em>
          </strong>
          . All Rights Reserved.
        </h2>
      </div>
    );
  }
}
