import React, { Component } from "react";
import Img from "../img/Flying around the world-pana.svg";

export default class Motto extends Component {
  constructor() {
    super();
    this.state = {
      ImgMotto: Img,
      ImgAlt: "ImgMotto",
    };
  }
  render() {
    return (
      <section className="flex flex-wrap w-[100%] px-[6%] py-[8%] bg-[#1a2138] items-center justify-between">
        <ArticleMotto />
        <ImgMotto
          loading="lazy"
          altImg={this.state.ImgAlt}
          img={this.state.ImgMotto}
        />
      </section>
    );
  }
}

function ArticleMotto() {
  return (
    <article className="w-11/12 xl:w-1/2 pt-[10%] mt-[4.3rem] pt-0 xl:mt-0 text-center xl:text-left">
      <h1 className="text-white text-[2.9rem] xl:text-[4.8rem]">
        Turn your ideas into a <span className="text-green-400">success.</span>
      </h1>
      <p className="text-[#aeb0b4] text-[20px] mt-6 leading-[2.5rem]">
        Webbee will make your product look modern and professional while saving
        you precious time.
      </p>
      <div className="btn w-full mt-10 flex justify-center xl:justify-start">
        <a
          href="#"
          className="text-white bg-green-400 py-[8px] px-[22px] rounded-[4px] mr-7 ease-in-out duration-500 border hover:border-green-400 hover:bg-transparent"
        >
          Purchase now
        </a>
        <a
          href="#"
          className="text-green-400 bg-transparent py-[7px] px-[21px] rounded-[4px] border border-green-400 ease-in-out duration-500 hover:bg-[#3c3c3c61]"
        >
          View documentation
        </a>
      </div>
    </article>
  );
}

class ImgMotto extends Component {
  render() {
    return (
      <figure className="w-11/12 md:w-7/12 mt-[5rem] xl:mt-0 md:mx-auto xl:mx-0 xl:w-1/3">
        <img
          className="w-full object-cover max-w-[100%]:"
          src={this.props.img}
          alt={this.props.altImg}
        />
      </figure>
    );
  }
}
