import React from "react";
import { Component } from "react";
export default class About extends Component {
  constructor() {
    super();
    this.state = {
      bigData: [
        {
          Img: (
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              stroke-width="1.5"
              stroke="currentColor"
              class="w-6 h-6"
            >
              <path
                stroke-linecap="round"
                stroke-linejoin="round"
                d="M15.042 21.672L13.684 16.6m0 0l-2.51 2.225.569-9.47 5.227 7.917-3.286-.672zm-7.518-.267A8.25 8.25 0 1120.25 10.5M8.288 14.212A5.25 5.25 0 1117.25 10.5"
              />
            </svg>
          ),
          Title: "Built for developers",
          desc: "Webbee is built to make your life easier. Variables, build tooling, documentation, and reusable components.",
        },
        {
          Img: (
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              stroke-width="1.5"
              stroke="currentColor"
              class="w-6 h-6"
            >
              <path
                stroke-linecap="round"
                stroke-linejoin="round"
                d="M3 13.125C3 12.504 3.504 12 4.125 12h2.25c.621 0 1.125.504 1.125 1.125v6.75C7.5 20.496 6.996 21 6.375 21h-2.25A1.125 1.125 0 013 19.875v-6.75zM9.75 8.625c0-.621.504-1.125 1.125-1.125h2.25c.621 0 1.125.504 1.125 1.125v11.25c0 .621-.504 1.125-1.125 1.125h-2.25a1.125 1.125 0 01-1.125-1.125V8.625zM16.5 4.125c0-.621.504-1.125 1.125-1.125h2.25C20.496 3 21 3.504 21 4.125v15.75c0 .621-.504 1.125-1.125 1.125h-2.25a1.125 1.125 0 01-1.125-1.125V4.125z"
              />
            </svg>
          ),
          Title: "Designed to be modern",
          desc: "Designed with the latest design trends in mind. Webbee feels modern, minimal, and beautiful.",
        },
        {
          Img: (
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              stroke-width="1.5"
              stroke="currentColor"
              class="w-6 h-6"
            >
              <path
                stroke-linecap="round"
                stroke-linejoin="round"
                d="M17.25 6.75L22.5 12l-5.25 5.25m-10.5 0L1.5 12l5.25-5.25m7.5-3l-4.5 16.5"
              />
            </svg>
          ),
          Title: "Documentation for everything",
          desc: "We've written extensive documentation for components and tools, so you never have to reverse engineer anything.",
        },
      ],
    };
  }
  render() {
    const { bigData } = this.state;
    return (
      <section id="about" className="w-[100%] bg-[#222B45]">
        <div className="row w-[100%] px-[6%] py-[4rem] flex flex-wrap justify-between">
          {bigData.map((val, index) => {
            return (
              <ShortStory
                key={index}
                img1={val.Img}
                title1={val.Title}
                desc1={val.desc}
              />
            );
          })}
        </div>
      </section>
    );
  }
}

class ShortStory extends Component {
  render() {
    return (
      <article className="w-11/12 xl:w-[32%] mb-10 mx-auto xl:mb-0 xl:mx-0 p-2 text-center">
        <Img img={this.props.img1} />
        <Title title={this.props.title1} />
        <Desc desc={this.props.desc1} />
      </article>
    );
  }
}

class Img extends Component {
  render() {
    return (
      <div className="w-full flex justify-center">
        <span className="w-[60px] h-[60px] bg-[#00ab551a] text-green-400 flex justify-center items-center rounded-[50%]">
          {this.props.img}
        </span>
      </div>
    );
  }
}

class Title extends Component {
  render() {
    return (
      <h2 className="w-full text-white my-4 text-[1.2rem]">
        {this.props.title}
      </h2>
    );
  }
}

class Desc extends Component {
  render() {
    return <p className="text-[#aeb0b4] leading-[36px]">{this.props.desc}</p>;
  }
}
