import React, { Component } from "react";

export default class LandingPage extends Component {
  constructor() {
    super();
    this.state = {
      bigData: [
        {
          Title: "Marketing",
          Desc: "Marketing Page",
          Btn: "View Demo",
        },
        {
          Title: "Marketing",
          Desc: "Marketing Page",
          Btn: "View Demo",
        },
        {
          Title: "Marketing",
          Desc: "Marketing Page",
          Btn: "View Demo",
        },
        {
          Title: "Marketing",
          Desc: "Marketing Page",
          Btn: "View Demo",
        },
        {
          Title: "Marketing",
          Desc: "Marketing Page",
          Btn: "View Demo",
        },
        {
          Title: "Marketing",
          Desc: "Marketing Page",
          Btn: "View Demo",
        },
        {
          Title: "Marketing",
          Desc: "Marketing Page",
          Btn: "View Demo",
        },
        {
          Title: "Marketing",
          Desc: "Marketing Page",
          Btn: "View Demo",
        },
        {
          Title: "Marketing",
          Desc: "Marketing Page",
          Btn: "View Demo",
        },
        {
          Title: "Marketing",
          Desc: "Marketing Page",
          Btn: "View Demo",
        },
        {
          Title: "Marketing",
          Desc: "Marketing Page",
          Btn: "View Demo",
        },
        {
          Title: "Marketing",
          Desc: "Marketing Page",
          Btn: "View Demo",
        },
      ],
    };
  }

  render() {
    return (
      <section id="demo" className="w-[100%]">
        <div className="row w-full px-[6%] py-12 bg-[#1A2138]">
          <Title />
          <Landing />
          <section className="w-full flex flex-wrap">
            {this.state.bigData.map((val, index) => {
              return (
                <Article
                  key={index}
                  title1={val.Title}
                  desc1={val.Desc}
                  btn1={val.Btn}
                />
              );
            })}
          </section>
        </div>
      </section>
    );
  }
}

function Title() {
  return (
    <section className="w-full p-4 text-center">
      <h4 className="text-[#AEB0B0]">DEMO PAGES</h4>
      <h2 className="text-white my-5 text-3xl xl:text-[2.9rem]">
        Webbee in action
      </h2>
      <p className="text-[#AEB0B0] text-xl xl:text-[1.5rem] leading-[40px] xl:mt-6">
        All examples you find below are included in the download package.
      </p>
      <div className="w-full flex justify-center mt-8">
        <a
          href="#"
          className="text-white bg-green-400 py-[8px] px-[22px] rounded-[4px] mr-7 ease-in-out duration-500 border hover:border-green-400 hover:bg-transparent"
        >
          Purchase now
        </a>
        <a
          href="#"
          className="text-green-400 bg-transparent py-[7px] px-[21px] rounded-[4px] border border-green-400 ease-in-out duration-500 hover:bg-[#3c3c3c61]"
        >
          View documentation
        </a>
      </div>
    </section>
  );
}

function Landing() {
  return (
    <div className="w-full text-center xl:text-left mb-11 mt-11">
      <h2 className="text-white text-[1.7rem]">Landing Pages</h2>
    </div>
  );
}

function Article(props) {
  return (
    <article className="w-11/12 mx-auto md:w-1/2 md:mx-0 xl:w-1/3 border border-[#aeb0b4] px-[35px] pt-[30px] pb-[7px] cursor-pointer">
      <TitleArticle title={props.title1} />
      <Desc desc={props.desc1} />
      <Button btn={props.btn1} />
    </article>
  );
}

function TitleArticle(props) {
  return <h2 className="w-full text-white text-[1.4rem]">{props.title}</h2>;
}

function Desc(props) {
  return <p className="w-full text-[#aeb0b4] mt-4">{props.desc}</p>;
}

function Button(props) {
  return (
    <div className="w-full py-5 flex justify-end">
      <a href="#" className="text-green-400 no-underline">
        {props.btn}
      </a>
    </div>
  );
}
