import React from "react";

export default function Advertisement() {
  return (
    <section className="w-[100%] px-[6%] py-14 bg-[#222B45] text-center">
      <Text />
    </section>
  );
}

function Text() {
  return (
    <div>
      <h4 className="text-[#aeb0b4]">GET STARTED</h4>
      <h2 className="text-[#fff] text-[2.2rem] xl:text-[2.9rem] my-4">
        Get started with Webbee today
      </h2>
      <p className="text-[#aeb0b4] leading-[35px]">
        Build a beautiful, modern website with flexible, fully customizable,
        atomic Material-UI components.
      </p>
      <div className="mt-9">
        <a
          href="#"
          className="text-white bg-green-400 py-[8px] px-[22px] rounded-[4px] mr-7 ease-in-out duration-500 border hover:border-green-400 hover:bg-transparent"
        >
          Purchase now
        </a>
      </div>
    </div>
  );
}
