import React from "react";
import { Component } from "react";
import ImgFeatures from "../img/Collaboration-amico.svg";

export default function Features() {
  return (
    <section className="w-[100%] bg-[#222B45]">
      <div className="row w-[100%] px-[6%] py-[4rem] flex flex-wrap justify-between text-center">
        <Text />
        <Img />
      </div>
    </section>
  );
}

function Text() {
  return (
    <>
      <h6 className="w-full text-[#aeb0b4]">FEATURES</h6>
      <h2 className="w-full xl:w-[54%] mx-auto text-white my-7 text-[2.9rem]">
        The powerful and flexible theme for all kinds of businesses
      </h2>
      <p className="w-full xl:w-[53%] mx-auto text-[#aeb0b4] leading-[36px] mb-7">
        Build a beautiful, modern website with flexible, fully customizable
        atomic Material-UI components. An experience you'd expect from a design
        system.
      </p>
      <div className="w-full flex justify-center">
        <a
          href="#"
          className="text-white flex items-center bg-green-400 py-[8px] px-[22px] rounded-[4px] ease-in-out duration-500 border hover:border-green-400 hover:bg-transparent"
        >
          Purchase now{" "}
          <i className="flex ml-3">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth={1.5}
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M17.25 8.25L21 12m0 0l-3.75 3.75M21 12H3"
              />
            </svg>
          </i>
        </a>
      </div>
    </>
  );
}

function Img() {
  return (
    <figure className="w-full mt-8 flex justify-center tr">
      <img
        loading="lazy"
        className="w-[100%] object-cover max-w-[600px] skew-x-3"
        src={ImgFeatures}
        alt="ImgFeatures"
      />
    </figure>
  );
}
